<template>
  <form @submit.prevent="submit">
    <div class="card my-3">
      <div class="card-header">
        <h5 class="card-title">Documentações</h5>
      </div>
      <div class="card-body">
        <!-- CPF -->
        <div class="form-group">
          <label for="ipt-cpf">CPF</label>
          <input id="ipt-cpf" type="text" class="form-control" disabled v-model="cpf">
        </div>
        <!-- RG - Número -->
        <div class="form-group">
          <label for="ipt-rg">Número do RG</label>
          <input id="ipt-rg" type="text" class="form-control" disabled v-model="rg">
        </div>
        <!-- RG - Órgão Emissor -->
        <div class="form-group">
          <label for="ipt-rgemissor">Orgão emissor do RG</label>
          <input id="ipt-rgemissor" type="text" class="form-control" disabled v-model="rgemissor">
        </div>
        <!-- RG - UF da emissão -->
        <div class="form-group">
          <label for="ipt-rgufemissor">Estado (UF) de emissão do RG</label>
          <input id="ipt-rgufemissor" type="text" class="form-control" disabled v-model="rgufemissor">
        </div>
        <!-- RG - Data da emissão -->
        <div class="form-group">
          <label for="ipt-rgdataemissao">Data da emissão do RG</label>
          <input id="ipt-rgdataemissao" type="text" class="form-control" disabled v-model="rgdataemissao">
        </div>
        <!-- Título Eleitoral - Número -->
        <div class="form-group">
          <label for="ipt-tituloeleitoral">Nº do Título de eleitor</label>
          <input id="ipt-tituloeleitoral" type="text" class="form-control" maxlength="12" v-model="tituloeleitoral">
        </div>
        <!-- Título Eleitoral - Zona -->
        <div class="form-group">
          <label for="ipt-zonaeleitoral">Zona eleitoral</label>
          <input id="ipt-zonaeleitoral" type="text" class="form-control" maxlength="3" v-model="zonaeleitoral">
        </div>
        <!-- Título Eleitoral - Secao -->
        <div class="form-group">
          <label for="ipt-secaoeleitoral">Seção eleitoral</label>
          <input id="ipt-secaoeleitoral" type="text" class="form-control" maxlength="3" v-model="secaoeleitoral">
        </div>
        <!-- Título Eleitoral - UF do Título é optativo, portanto segue descartado deste projeto -->
      </div>
      <div class="card-footer">
        <button type="submit" class="btn btn-block btn-primary mx-0">AVANÇAR</button>
        <router-link :to="{name: 'recad-form-dadospessoais'}" class="btn btn-block btn-secondary mx-0">VOLTAR</router-link>
      </div>
    </div>
  </form>
</template>

<script>
  import DateHelper from "../../../helpers/DateHelper";
  export default {
    name: "Documentacao",
    data: () => ({
      cpf: '',
      rg: '',
      rgemissor: '',
      rgufemissor: '',
      rgdataemissao: '',
      tituloeleitoral: '',
      zonaeleitoral: '',
      secaoeleitoral: ''
    }),
    created() {
      //Alimentar campos
      if (!sessionStorage.getItem('recad_documentacao')) {
        //Carrega da Store
        this.cpf = this.$store.getters.cpf;
        this.rg = this.$store.state.rg;
        this.rgemissor = this.$store.state.rgemissor;
        this.rgufemissor = this.$store.state.rgufemissor;
        this.rgdataemissao = DateHelper.date_SQLparaBR(this.$store.state.rgdataemissao);
        this.tituloeleitoral = this.$store.state.tituloeleitoral;
        this.zonaeleitoral = (this.$store.state.zonaeleitoral) ? parseInt(this.$store.state.zonaeleitoral).toString() : this.$store.state.zonaeleitoral;
        this.secaoeleitoral = (this.$store.state.secaoeleitoral) ? parseInt(this.$store.state.secaoeleitoral).toString() : this.$store.state.secaoeleitoral;
      } else {
        //Carrega da sessionStorage
        const dados = JSON.parse(sessionStorage.getItem('recad_documentacao'));
        this.cpf = dados.cpf;
        this.rg = dados.rg;
        this.rgemissor = dados.rgemissor;
        this.rgufemissor = dados.rgufemissor;
        this.rgdataemissao = dados.rgdataemissao;
        this.tituloeleitoral = dados.tituloeleitoral;
        this.zonaeleitoral = dados.zonaeleitoral;
        this.secaoeleitoral = dados.secaoeleitoral;
      }
    },
    beforeRouteLeave (to, from, next) {
      sessionStorage.setItem('recad_documentacao', JSON.stringify({
        cpf: this.cpf,
        rg: this.rg,
        rgemissor: this.rgemissor,
        rgufemissor: this.rgufemissor,
        rgdataemissao: this.rgdataemissao,
        tituloeleitoral: this.tituloeleitoral,
        zonaeleitoral: this.zonaeleitoral,
        secaoeleitoral: this.secaoeleitoral
      }));
      next();
    },
    methods: {
      submit() {
        this.$router.push({name: 'recad-form-endereco'});
      }
    }
  }
</script>
